import { Link, useLoaderData } from "remix";

type Post = {
  slug: string;
  title: string;
}

export const loader: Post[] = () => {
  return [
    {
      slug: 'my-post',
      title: 'my post'
    },
    {
      slug: 'batatinha',
      title: 'i have no idea'
    }
  ];
};

export default function Posts() {
  const posts = useLoaderData();
  console.log(posts);
  return (
    <div>
      <h1>Posts</h1>
      <ul>
        {posts.map(post => (
          <li key={post.slug}>
            <Link to={post.slug}>{post.title}</Link>
          </li>
        ))}
      </ul>
    </div>
  );
}
